<template>
  <div class="dealer">
    <h3 class="title">核销平台登陆</h3>
    <div>
      <mt-field label="账号" placeholder="请输入您的账号" v-model="account"></mt-field>
      <mt-field type="password" label="密码" placeholder="请输入您的密码" v-model="password"></mt-field>
      <router-link to="/dealer/forget" style="display: block;text-align: right;color: #999;">忘记密码？</router-link>
      <mt-button type="primary" size="large" style="margin-top: 1rem" v-on:click.native="clickLogin">登陆</mt-button>
    </div>
  </div>
</template>

<script>
import {dealerLogin} from '../../api/maintain'
import * as types from '../../store/mutation-types'
export default {
  name: 'Dealer-Login',
  data () {
    return {
      account: '',
      password: ''
    }
  },
  methods: {
    clickLogin () {
      if (!this.account || !this.password) {
        return this.$toast('请输入完整')
      }
      dealerLogin({account: this.account, password: this.password})
        .then(data => {
          if (data.state) {
            this.$toast('登陆成功')
            this.$store.commit(types.CHANGE_DEALER_LOGIN_STATE, true)
            this.$store.commit(types.SET_DEALER_LOGIN_INFO, JSON.stringify(data.data))
            return this.$router.replace('/dealer')
          }
          return this.$toast(data.errormsg || '用户名或密码错误')
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试')
        })
    }
  }
}
</script>

<style lang="less" scoped>
.dealer{
  .title {
    text-align: center;
  }
}
</style>



