<template>
  <div class="login-password">
    <mt-field :attr="{id: 'tel'}" label="手机"  placeholder="请输您的手机号码" v-model="username"></mt-field>
    <mt-field :attr="{id: 'psw'}" label="密码"  type="password" placeholder="请输入密码" v-model="password"></mt-field>
    <mt-checklist
      v-model="auto"
      :options="['自动登陆']">
    </mt-checklist>
    <mt-field v-show="needVcode" label="验证码" v-model="vcode" placeholder="请输入验证码">
      <img :src="vcodeAddr" height="45px" width="100px" @click="refreshVCode">
    </mt-field>
    <div class="desc">
      <router-link to="/login/forget" style="display: block;color: #2d8cf0;">忘记密码？</router-link>
      <!--<p>还没有重卡汇账号？<a href="javascript:void (0)">立即免费注册</a></p>-->
    </div>
    <mt-button type="primary" size="large" style="margin-top: 1rem" v-on:click.native="clickLogin">登陆</mt-button>
    <mt-button type="primary" size="large" style="margin-top: 1rem" v-on:click.native="goTo('/login?type=dealer')">商户登陆</mt-button>
  </div>
</template>

<script>
import {Toast} from 'mint-ui'
import {loginForPassword, getUserInfo} from '../../api/login'
import {ROOT} from '../../config/urls'
export default {
  name: 'LoginPassword',
  data () {
    return {
      username: '',
      password: '',
      auto: '',
      vcode: '',
      vcodeAddr: '',
      needVcode: false
    }
  },
  mounted () {
    this.$nextTick(() => {
      let telEl = document.getElementById('tel')
      let pswEl = document.getElementById('psw')
      let barEl = document.getElementsByClassName('mint-tabbar')[0]
      let wrapEl = document.getElementById('loginWarpper')
      telEl.onfocus = function () {
        wrapEl.style.paddingTop = '50px'
        barEl.style.display = 'none'
      }
      telEl.onblur = function () {
        wrapEl.style.paddingTop = '100px'
        barEl.style.display = 'flex'
      }
      pswEl.onfocus = function () {
        wrapEl.style.paddingTop = '50px'
        barEl.style.display = 'none'
      }
      pswEl.onblur = function () {
        wrapEl.style.paddingTop = '100px'
        barEl.style.display = 'flex'
      }
    })
  },
  methods: {
    clickLogin () {
      loginForPassword(this.username, this.password, this.vcode, this.auto)
        .then(data => {
//          console.log(window.cookie)
          if (data.state) {
            if (this.auto) {
              window.Cookie.set('PHPSESSID', data.data.info.session, {expires: 7, path: 'api.hongyanche.com'})
            } else {
              window.Cookie.set('PHPSESSID', data.data.info.session, {path: 'api.hongyanche.com'})
            }
            getUserInfo().then(data => {
              console.log(window.cookie)
              if (data.errormsg === 'not login') {
                Toast(data.errormsg || '登陆失败')
              } else if (!data.state) {
                Toast(data.errormsg || '登陆失败')
              } else {
                let datas = data.data.info
                this.$store.dispatch('userLoginAction', datas)
                this.$router.go(-1)
              }
            })
          } else {
            Toast(data.errormsg)
          }
        }).catch(e => {
          if (e === 'vcode') {
            this.$toast('请输入验证码')
            this.needVcode = true
            this.vcodeAddr = ROOT + '/vcode'
          }
        })
    },
    refreshVCode () {
      let _random = Math.random() * 100
      this.vcodeAddr = ROOT + '/vcode?r=' + _random
    },
    goTo (path) {
      this.$router.replace(path)
    }
  }
}
</script>

<style lang="less" scoped>
  .login-password{
    border-radius: 5px;
    .desc{
      margin-top: 1rem;
      font-size: 14px;
      p {
        margin: 0;
      }
    }
  }
</style>



