<template>
  <div class="login-wrap">
    <app-header title="重卡汇" :showBack="showBack"></app-header>
    <div id="loginWarpper" class="container login" v-if="!isDealer">
      <mt-navbar v-model="selected">
        <mt-tab-item id="login">账号密码登陆</mt-tab-item>
        <mt-tab-item id="register">手机短信注册</mt-tab-item>
      </mt-navbar>
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="login">
          <LoginPasswrod></LoginPasswrod>
        </mt-tab-container-item>
        <mt-tab-container-item id="register">
          <LoginRegister></LoginRegister>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <div class="container login" v-else>
      <dealer-login></dealer-login>
    </div>
  </div>
</template>

<script>
import LoginPasswrod from './password.vue'
import LoginRegister from './register.vue'
import AppHeader from '../../components/header/header.vue'
import DealerLogin from './dealer-login'
import {getQueryString} from '../../lib/util'
export default {
  name: 'Login',
  data () {
    return {
      selected: 'login',
      showBack: false,
      isDealer: false
    }
  },
  created () {
    if (getQueryString('type') === 'dealer') {
      this.isDealer = true
    }
  },
  watch: {
    '$route': function () {
      window.location.reload()
    }
  },
  components: {
    LoginPasswrod,
    LoginRegister,
    AppHeader,
    DealerLogin
  }
}
</script>

<style lang="less" scoped>
  .login-wrap {
    height: 100%;
    width: 100%;
    position: fixed;
    background-color: #f0eff5;
    .login {
      padding: 100px 20px 200px 20px;
      .mint-navbar{
        margin-bottom: 2rem;
        background-color: #f0eff5;
      }
    }
  }
</style>



